  TextFilenotatitip() {
    const element = document.createElement("a");
    const file = new Blob([document.getElementById("nota_titip").value], {
      type: "text/plain;charset=utf-8",
    });
    element.href = URL.createObjectURL(file);
    element.download = "autoprint_titipan.txt";
    document.body.appendChild(element);
    element.click();
  }
  simpanPembayaran(data) {
    this.setState({
      isLoadingPembayran: true,
      onSend: true,
    });
    let harus_bayar = document.getElementById("harus_bayar_rp").value;
    let harus_bayar2 = harus_bayar.replace("Rp.", "");
    let harus_bayar3 = harus_bayar2.replaceAll(".", "");
    let data_barang_penjualan = JSON.parse(
      localStoragedecryp("data_barang_penjualan_tmp")
    );
    let detail_barang = [];

    data_barang_penjualan.forEach((item, index) => {
      let row = {
        no_titip_group: item.no_titip_group || "-",
        no_pesanan: item.no_pesanan || "-",
        kode_barcode: item.kode_barcode,
        berat: item.berat_jual,
        berat_atribut: item.berat_atribut,
        harga_gram: item.harga_pergram,
        ongkos: item.ongkos,
        harga_jual: item.harga_jual,
        harga_atribut: item.harga_atribut,
      };
    
      detail_barang.push(row);
    });
    let pembayaran = getItem("trx_pemabayran_penjualan");
    let data_pembayaran = [];
    let total_pembayarn = 0;
    if (pembayaran.length !== 0) {
      
    pembayaran.forEach((element) => {
      let row = {
        bank : element.jenis !== "CASH" ? element.nama_bank : "CASH", 
        jenis: element.jenis,
        keterangan: element.keterangan,
        
        jumlah_rp: element.jenis === "CASH" ? parseInt(element.nominal) - parseInt(getItem('sisa_bayar_penjualan').sisa_bayar) : parseInt(element.nominal),
        // jumlah_rp: element.nominal > parseInt(getItem('sisa_bayar_penjualan').total_transaksi) ? parseInt(element.nominal) - parseInt(getItem('sisa_bayar_penjualan').sisa_bayar) : parseInt(element.nominal),
      };
      total_pembayarn = element.nominal + total_pembayarn;
      data_pembayaran.push(row);
    });
  }

    let sisa = parseInt(harus_bayar3) - parseInt(total_pembayarn);

    if (parseInt(sisa) > 0) {
      ToastNotification(
        "info",
        "Pembayaran Masih Kurang Rp." + parseInt(sisa).toLocaleString("ID-id")
      );
      this.setState({
        isLoadingPembayran: false,
        onSend: false,
      });
      return false;
    }

    let data_customer = JSON.parse(localStoragedecryp("data_customer_tmp"));
    
    let datapenjualan = {
      status: "PENJUALAN",
      kode_sales: data_customer.kode_sales,
      kode_member:
        data_customer.jenis_pelanggan === "NONMEMBER"
          ? data_customer.jenis_pelanggan
          : data_customer.kode_customer,
      nama_customer: data_customer.nama_customer,
      alamat_customer: data_customer.alamat_customer,
      no_hp: data_customer.no_hp,
      detail_barang: detail_barang,
      pembayaran: data_pembayaran,
    };
    
    // if (parseInt(data.cash) < parseInt(data.grand_total_pembayaran)) {
    //   let cash_bayar =
    //     parseInt(data.grand_total_pembayaran) - parseInt(data.cash);
    //   ToastNotification(
    //     "info",
    //     "Pembayaran Masih Kurang Rp." +
    //       parseInt(cash_bayar).toLocaleString("ID-id")
    //   );
    //   this.setState({
    //     isLoadingPembayran : false
    //   })
    //   return false;
    // }
    // localStorage.setItem("hasil", JSON.stringify(datapenjualan));
    postData("penjualan/simpan", datapenjualan)
      .then((res) => {
        console.log(res.data);
        localStorageencryp("data_barang_penjualan_tmp", JSON.stringify([]));
        ToastNotification("success", res.data.message);
      })
      .then(() => {
        this.props.dispatch(hideModal());
        this.props.dispatch(setCamera(false));
      })
      .then(() => {
        this.props.dispatch(reset("ModalPembayaran"));
      })
      .then(() => {
        localStorageencryp("data_customer_tmp", JSON.stringify([]));
        localStorageencryp("total_dp", JSON.stringify([]));
        localStorageencryp("trx_pemabayran_penjualan", JSON.stringify([]));
        localStorageencryp("filter_titipan", JSON.stringify([]));
        setItem('sisa_bayar_penjualan', [])
        setItem('sisa_bayar_penjualan',0)
      })
      .then(() => {
        this.setState({
          isLoadingPembayran: false,
          onSend: false,
        });
        this.props.dispatch(setBarangPenjualan([]));
      })
      .catch((err) =>
        {
          this.setState({
            isLoadingPembayran: false,
            onSend: false,
          });
          this.errorhandling(err.response.data || "Koneksi Bermasalah")
        }
      );
    // localStorage.setItem("tmp_pembayaran_transaksi_penjualan",JSON.stringify(hasil));
  }

